.header{
    display: flex;
    justify-content: space-between;
    padding: 15px; 
    border-bottom: 1px solid;
    margin-bottom: 50px;
}

.header-left{
    display: flex;
    align-items: center;
    width: 30%;
}

.list{
    display: flex;
    list-style: none;
    align-items: center;
}

li{
    margin: 10px;
}

.container{
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

input{
   background: silver;
   border: 1px solid #393939;
   border-radius: 5px 5px 5px 5px;
   color: #393939;
   font-size: 12px;
   padding: 5px;
}

.container-info{
    padding: 15px;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 15px;
    width: 85%;
    background-color: #c9c9c9;
}

.input-container{
    display: flex;
    justify-content: center;
}

.imagen-example{
    width: 100%;
}

.imagen-ateneo{
    width: 90%;
}

.header-left-span{
    width: 50%
}

p{
    line-height: 1.5;
    margin-bottom: 10px;
}

#last-p{
    margin-bottom: 15px;
}

.p-info{
    text-align: center;
}

.chart-space{
    display: flex;
    justify-content: center;
}

.chart{
    margin-bottom: 15px;
}

text{
    fill: rgb(0, 0, 0);
}

hr{
    width: 100%;
    margin-bottom: 60px;
}

.chart-reaction{
    margin-left: 60px;

}

.footer{
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    border-top: 1px solid black;
}

.chart-info{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

g{
    width: 70%;
}

.icons{
    width: 90%;
}

rect{
    justify-content: center;
}

.chart-kpi{
    width: 25%;
    height: 25%;
}

.container-kpis{
    display: flex;
    justify-content: center;
    gap: 40px;
    margin-bottom: 30px;;
}